import React from "react";
const Slider = () => {
    return (
    <>
                <div id="Slide" class="carousel slide" data-ride="carousel">
            <div class="carousel-inner">
                <div class="carousel-item active">
                    <img class="d-block w-100" src="https://picsum.photos/1366/400" alt="First slide"/>
                </div>
                <div class="carousel-item">
                    <img class="d-block w-100" src="https://picsum.photos/1366/400" alt="Second slide"/>
                </div>
                <div class="carousel-item">
                    <img class="d-block w-100" src="https://picsum.photos/1366/400" alt="Third slide"/>
                </div>
            </div>
        </div>

    


    </>)
}
export default Slider;